<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
              :src="require('../../assets/icons/home-01.png')"
              contain
              height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">Mi abogado LegalHelp</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Tipo de consulta</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ service.name }}</div>

          <div class="mb-5" v-html="service.description"></div>

          <p class="text-center">Costo del documento : S/. {{ service.price }}</p>

          <coupon
              :price="service.price"
              @code-used="onCodeUsed"
              @price-updated="onPriceUpdated"
          ></coupon>

          <p class="text-center">¿Cómo quieres pagar?</p>

          <div v-if="false" class="call-to-action" @click="goToPayment()">
            <div class="icon">
              <v-img :src="require('../../assets/icons/lawyer-05.svg')" contain height="24"/>
            </div>
            <div class="text">Pago con tarjeta</div>
          </div>

          <div class="call-to-action" @click="goToBank()">
            <div class="icon">
              <v-img :src="require('../../assets/icons/lawyer-06.svg')" contain height="24"/>
            </div>
            <div class="text">Depósito bancario</div>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import Coupon from "../../components/Coupon";

export default {
  name: 'MyLawyerPaymentType',
  components: {Coupon},
  data: () => ({
    priceTotal: 0,
    code: '',
  }),
  computed: {
    ...mapGetters(['user', 'services', 'service']),
  },
  created() {
    if (this.service === null) {
      let serviceId = parseInt(this.$route.params.id)
      let service = this.services.find(obj => obj.id === serviceId)
      this.selectService(service)
    }
  },
  mounted() {

  },
  methods: {
    ...mapActions(['selectService', 'addCodeToService', 'addPriceToService']),
    goToPayment() {
      let serviceId = this.$route.params.id
      this.$router.push({name: 'MyLawyerPayment', params: {id: serviceId}})
    },
    goToBank() {
      let serviceId = this.$route.params.id
      this.$router.push({name: 'MyLawyerBank', params: {id: serviceId}})
    },
    onCodeUsed(code) {
      this.code = code
      this.addCodeToService(code)
    },
    onPriceUpdated(price) {
      this.priceTotal = price
      this.addPriceToService(price)
    }
  }
}
</script>
